import React, {useState} from 'react';
import { setStorage, isItemInStorage, removeFromStorage } from "../../utilities/storageMaker";

const SinglePageInfo = ({movieInfo}) => {
    const [faved, setFaved] = useState(isItemInStorage(movieInfo));
    //console.log(movieInfo);
    let VoteInPercentage = Math.round((movieInfo.vote_average / 10) * 100);

    const addToFavs = () => {
        setStorage(movieInfo);
        setFaved(true);
    }

    const removeFromFavs = () => {
        removeFromStorage(movieInfo);
        setFaved(false);
    }

    return (
        <div>
            <img className='backdrop' src={`https://image.tmdb.org/t/p/w780/${movieInfo.backdrop_path}`} alt="backdrop"/>
            <div className="overlay">
                <h1>{movieInfo.title}</h1>
                <h3 className='release-date'>({movieInfo.release_date})</h3>    
            </div> {/* end of overlay */}
            <div className='inner-wrapper'>
                <img className='poster' src={`https://image.tmdb.org/t/p/w600_and_h900_bestv2/${movieInfo.poster_path}`} alt="poster"/>
                <div className="overview-info">
                    <h1>{VoteInPercentage}%</h1>
                    <h4>"{movieInfo.tagline}"</h4>
                    <h3>Overview:</h3>
                    <p>{movieInfo.overview}</p>
                    <a href={`${movieInfo.homepage}`}>Visit the Homepage</a>
                    <br/>
                    {faved === false ? <button className="btn" onClick={addToFavs}>Add To Favourites</button> :
                    <button className="btn" onClick={removeFromFavs}>Remove From Favourites</button>}
                </div> {/* end of overview */}
            </div> {/* end of inner-wrapper */}
        </div>
    )
}
export default SinglePageInfo;
