import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from '../components/pages/Home';
import About from '../components/pages/About';
import Favourites from '../components/pages/Favourites';
import SinglePage from '../components/pages/SinglePage';
import Team404 from '../components/pages/Team404';
import Header from '../components/Header';
import Footer from '../components/Footer';
import TOP_RATED from '../components/pages/movies-sort/TopRated';
import POPULAR from '../components/pages/movies-sort/Popular';
import NOW_PLAYING from '../components/pages/movies-sort/Now-Playing';
import UPCOMING from '../components/pages/movies-sort/Upcoming';


const AppRouter = () => (
    <Router>
        <div className="outer-wrap">
            <Header/>
            <div className='wrapper' basename={"/cinewatch"}>
            <Switch>
                <Route path={"/"} exact><Home/></Route>
                <Route path={"/about"}><About/></Route>
                <Route path={"/favourites"}><Favourites/></Route>
                <Route path={"/popular"}><POPULAR/></Route>
                <Route path={"/top-Rated"}><TOP_RATED/></Route>
                <Route path={"/now-playing"}><NOW_PLAYING/></Route>
                <Route path={"/upcoming"}><UPCOMING/></Route>
                <Route path={"/page/:id"}><SinglePage/></Route>
                <Route path={"/*"} exact><Team404/></Route>
            </Switch>
            </div>
            <Footer />
        </div>
    </Router>
);
  
export default AppRouter;