import React from 'react';
// import Placeholder from '../images/placeholder.png';
import {Link} from 'react-router-dom';
import Main from '../MoviesSort';
const MovieTile = ({movies, sort}) => {
    
    // const movie = [{movie}];

    //console.log(movies)

    const generateMovieTile = (arr) => {
        
        return arr.map((movie, i) => {

            // Get this specific ID (generated via 'movie' state) from this array
            // to another page. Through that 

            // logic for opening Modal
            //const [openModal, setOpenModal] = useState(false);

            return (
                <div className={`film-${i + 1} box`} key={movie.id}>
                    <img src={`https://image.tmdb.org/t/p/w600_and_h900_bestv2/${movie.poster_path}`} alt="placeholder poster"/>
                    <Link to={`/page/${movie.id}`}>
                    <div className="film-summary">
                        <h3 className="film-title">{movie.title}</h3>
                        <p className="date">{movie.release_date}</p>
                        <h4>{`${movie.vote_average} / 10`}</h4>
                        <p className="more-info-tap">Click/Tap For More Info</p>
                        <p>{movie.overview}</p>
                        <p className="more-info">More Info</p>
                    </div>
                    </Link>
                </div>
            );
        });
    };


    return (    
        <div className="Main">
            {sort && <Main />}
            <div className="film-container">
              {generateMovieTile(movies)}  
            </div>
        </div>
    );
}


MovieTile.defaultProps = {
    sort: true
}

export default MovieTile;