import React, {useState, useEffect} from 'react';
//import { movieTile } from '../../utilities/filmMakerFunctions';
// syntaxs goes like this POPULAR_MOVIES API_KEY_ENG_LANG_PAGE_ONE
import MovieTile from '../../partials/MovieTile';
import { API_KEY, ENG_LANG, PAGE_ONE, NOW_PLAYING_MOVIES} from "../../../global/variables";


const NOW_PLAYING = () => {

    // Set Template:
    const [movies, setMovies] = useState(null);
    
    // set state variables 
    // const [movieData ,setMovieTile] = useState(null);

    //this generates the movie popular 
    useEffect(() => {
        const fetchMovie = async () => {
    

                const res = await fetch(`${NOW_PLAYING_MOVIES}${API_KEY}${ENG_LANG}${PAGE_ONE}`);
                const data = await res.json();

                //const movieData = data.results;
                const movieData = data.results;
                setMovies(movieData);
                //console.log(movieData);

            

        }
        fetchMovie();
    }, []);

    
    return (
        <div className="Now-Playing">
            <h1>Now Playing</h1>
            { movies !== null && <MovieTile movies={movies} />}
        </div>
    )
};

export default NOW_PLAYING;